import { reactive } from "vue"

export const useGolfSpec = () => {
	const golfSpecs = reactive({
		length: {
			selected: null,
			is_custom: false
		},
		loft: {
			selected: null,
			is_custom: false
		},
		gender: {
			selected: null,
			is_custom: false
		},
		shaft_model: {
			selected: null,
			is_custom: false
		},
		shaft_material: {
			selected: null,
			is_custom: false
		}
	})

	const transformSeperateCustomSpecs = (specs = {}) => {
		const _golfSpecs = {
			length: null,
			loft: null,
			gender: null,
			shaft_model: null,
			shaft_material: null,
			custom_length: null,
			custom_loft: null,
			custom_gender: null,
			custom_shaft_model: null,
			custom_shaft_material: null,
		}

		Object.entries(specs).forEach(([key, spec]) => {
			const { selected: selectedSpec = null, is_custom: isCustomSpec = false } = spec || {}
			if (_golfSpecs.hasOwnProperty(key)) {
				if (isCustomSpec) _golfSpecs[`custom_${key}`] = selectedSpec
				else _golfSpecs[key] = selectedSpec
			}
		})
		return _golfSpecs
	}

	return {
		golfSpecs,
		transformSeperateCustomSpecs,
	}
}